<script setup>
import { defineProps, defineEmits } from "vue";

//  `items`  module_id  name `modelValue`
const props = defineProps({
  items: {
    type: Array,
    required: true
  },
  modelValue: {
    type: Array,
    default: () => []
  }
});

// 
const emit = defineEmits(["update:modelValue", "change"]);

// 
const toggleSelection = (item) => {
  const selected = props.modelValue.map((selectedItem) => ({ ...selectedItem })); // 

  //  module_id 
  const index = selected.findIndex(selectedItem => selectedItem.module_id === item.module_id);

  if (index !== -1) {
    // 
    selected.splice(index, 1);
  } else {
    // 
    selected.push({ ...item }); // 
  }

  //  v-model 
  emit("update:modelValue", selected);
  emit("change", selected);
};
</script>

<template>
  <div>
    <label></label>
    <div v-for="item in items" :key="item.module_id" class="check_list">
      <input
        type="checkbox"
        :id="'checkbox-' + item.module_id"
        :checked="modelValue.some(selectedItem => selectedItem.module_id === item.module_id)"
        @change="toggleSelection(item)"
      />
      <label :for="'checkbox-' + item.module_id">{{ item.name }}</label>
    </div>
  </div>
</template>

<style scoped>
.check_list {
  display: flex;
  flex-direction: row;
  justify-content: start;
  width: 100%;
  padding-left: 30%;
}
</style>
